html {
  font-size: 100%;
  box-sizing: border-box;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeSpeed;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-primary);
  background-color: var(--blue-900);
}

:root {
  --blue-900: #002547;
  --blue-800: #073464;
  --blue-700: #124982;
  --blue-600: #2d6095;
  --blue-500: #487cb1;
  --blue-400: #6698cc;
  --blue-300: #9dbee1;
  --blue-200: #c6d6e6;
  --blue-100: #dce3e9;
  --yellow-900: #663d00;
  --yellow-800: #995c00;
  --yellow-700: #c97a03;
  --yellow-600: #ee9611;
  --yellow-500: #e8a94a;
  --yellow-400: #eebf77;
  --yellow-300: #ffd494;
  --yellow-200: #ffe8c7;
  --yellow-100: #fff2e0;
}

:root {
  --font-primary: "Proxima Nova", "Trebuchet MS", sans-serif;
}

.grid {
  display: grid;
}

.grid-col__2fr-1fr {
  grid-template-columns: 2fr 1fr;
}
.grid-col__3fr-1fr {
  grid-template-columns: 3fr 1fr;
}

.flex {
  display: flex;
}
.flex-col {
  display: flex;
  flex-direction: column;
}

.flow-wrap {
  flex-flow: wrap;
}

.jc-sb {
  justify-content: space-between;
}
.jc-sa {
  justify-content: space-around;
}
.jc-c {
  justify-content: center;
}
.jc-start {
  justify-content: flex-start;
}
.jc-end {
  justify-content: flex-end;
}

.ai-start {
  align-items: flex-start;
}
.ai-end {
  align-items: flex-end;
}
.ai-c {
  align-items: center;
}
.ai-base {
  align-items: baseline;
}

.gap-01 {
  gap: 0.1rem;
}
.gap-02 {
  gap: 0.2rem;
}
.gap-03 {
  gap: 0.3rem;
}
.gap-05 {
  gap: 0.5rem;
}
.gap-1 {
  gap: 1rem;
}
.gap-15 {
  gap: 1.5rem;
}
.gap-2 {
  gap: 2rem;
}
.gap-3 {
  gap: 3rem;
}
.gap-4 {
  gap: 4rem;
}
.gap-5 {
  gap: 5rem;
}

.w-100 {
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

.--bold {
  font-weight: 600;
}

ul {
  margin: 0;
  padding: 0;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4375rem;
  padding: 0.4375rem 0.875rem;
  border-radius: 0.3125rem;
  font-weight: 600;
}
@media (max-width: 377px) {
  .button {
    font-size: 0.9rem;
  }
}
.button--primary {
  background-color: var(--yellow-400);
  color: var(--blue-900);
}

/* === ABOUT CARD === */
.about {
  position: relative;
  z-index: 99;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  padding: 1.875rem;
  max-width: 41ch;
  margin: -3.75rem auto 0;
  background: linear-gradient(180deg, var(--blue-300) 0%, var(--blue-400) 100%);
  box-shadow: 0px 12px 24px rgba(0, 19, 36, 0.25);
  border-radius: 0.625rem;
}
@media (max-width: 377px) {
  .about {
    margin: -3.75rem 1.875rem 0;
  }
}
.about__heading {
  font-size: 1.125rem;
  color: var(--blue-900);
  margin-bottom: -0.3125rem;
}
.about__subheading {
  font-size: 0.5625rem;
  color: var(--blue-500);
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
}
.about__body {
  font-size: 0.875rem;
  color: var(--blue-900);
  line-height: 1.4;
}
.about__list {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  margin-top: 0;
}
.about__list-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  list-style-type: none;
  text-align: center;
  line-height: 1.2;
}
@media (max-width: 349px) {
  .about__list-item {
    font-size: 0.75rem;
  }
}
.about__list-item img {
  height: 4rem;
}
@media (max-width: 349px) {
  .about__list-item img {
    height: 2rem;
  }
}

.navbar {
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 99999;
  background-color: var(--blue-900);
  padding: 0.9375rem 1.875rem;
}
.navbar__logo-container {
  display: inline-flex;
}
@media (max-width: 585px) {
  .navbar__logo {
    max-width: 150px;
  }
}
@media (max-width: 349px) {
  .navbar__logo {
    display: none;
  }
}
.navbar__logo--mobile {
  display: none;
}
@media (max-width: 349px) {
  .navbar__logo--mobile {
    display: block;
  }
}

.hero {
  position: relative;
  background-image: linear-gradient(145deg, var(--blue-900) 45%, var(--blue-800) 75%);
  background-color: var(--blue-900);
  background-attachment: scroll;
  background-repeat: no-repeat;
  background-size: cover;
  height: 35.9375rem;
  padding: 0 1.875rem;
}
.hero::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  background-image: url(../../../img/bg-stars.png);
  background-repeat: repeat-y;
  background-size: contain;
  opacity: 0.1;
}
@media (min-width: 586px) {
  .hero::after {
    background-size: cover;
  }
}
.hero__text-container {
  padding-top: 5.625rem;
}
.hero__heading-container {
  margin: 0.375rem 0;
}
.hero__heading {
  margin: 0;
  color: #fff;
  text-transform: uppercase;
  font-size: 2.625rem;
  line-height: 0.9;
  clip-path: inset(0 0 0 0);
  display: inline-flex;
}
.hero .clip-container {
  clip-path: inset(0 0 0 0);
}
.hero__body {
  color: var(--blue-300);
  display: inline-block;
}
.hero__image-container {
  position: absolute;
  z-index: 2;
  right: 0;
  bottom: 0;
  transform-origin: right top;
  transform: scale(1.5) translateY(-5.625rem);
  transition: transform 200ms ease-in;
}
@media (max-width: 585px) {
  .hero__image-container {
    transform: scale(1.3) translateY(-1.875rem);
  }
}
@media (max-width: 377px) {
  .hero__image-container {
    transform: scale(0.8) translateY(0);
  }
}
.hero__image {
  width: 100%;
}